/**
 * banner页相关
 */
import Url from '../url.js';
// import HOST from '@/config';
import axios from '../http';

/**
 * 获取动态活动页详情
 */
export const getCustomPage = params => axios.get(Url.GET_CUSTOM_PAGE, { params });

/**
 * 获取主题活动详情
 */
export const getActivityPage = params => axios.get(Url.GET_ACTIVITY_PAGE, { params });

/**
 * 获取优惠券详情 couponId=40189
 */
export const getCouponDetail = params => axios.get(Url.GET_COUPON_DETAIL, { params });

/**
 * 设置领取优惠券提醒
 */
export const setCouponRemind = params => axios.get(Url.REMIND_COUPONS_LIST, { params });

/**
 * 领取优惠券
 */
export const getCoupon = params => axios.get(Url.receiveCoupon, { params });

/**
 * 是否为微店主接口
 */
export const isWdHost = (params) => axios.get(Url.isWdHost, { params });

export default {
  getCustomPage,
  getActivityPage,
  getCouponDetail,
  setCouponRemind,
  getCoupon,
  isWdHost,
};
