<template>
  <div style="background:white; height: 100%">
    <skeleton :nums="4" :iheight="'.4rem'" v-if="isSkeletonLoading"/>
    <div v-else>
      <div class="container" >
        <div class="coupon-container" v-if="detail">
            <div class="content-wrapper">
              <div class="value-wrapper">
                <coupon-value-label style='background-color: transparent;' :couponValue='detail.couponValue' :couponType='detail.couponType'></coupon-value-label>
              <!-- <span class="val-icon">￥</span>
              <span class="val-num" :class="{bigVal:isBigVal}">{{detail.couponValue}}</span> -->
            </div>
            <div class="info-wrapper">
              <div class="info">
                <p class="info-text">
                  <span v-if="detail.scope==='PRODUCT'" class="singleItem">单品</span>
                  <span v-if="detail.scope==='ORDER'" class="singleItem">订单</span>
                  {{detail.minAmountDoc}}
                </p>
                <p class="info-text">{{detail.couponTemplateName}}</p>
                <p class="info-text">{{detail.startTime}}—{{detail.endTime}}</p>
                <!-- <van-progress :show-pivot="false" stroke-width="0.12rem" color="#ED2856" track-color="rgba(237,40,86,.2)" :percentage="detail.percent" /> -->
              </div>
              <div class="receive">
                <div class="percent-container" v-if="detail.displayType !== '1'"/>
                  <div v-if="detail.displayType==='1'" class="not-received">
                    <span class="receive-btn" @click.stop="getCoupon()">立即领取</span>
                  </div>
                  <div v-else-if="detail.displayType==='2'" class="received">
                    <span class="receive-disabled" @click.stop="useCoupon">立即使用</span>
                    <img :src="`${$cdnImageUrl}/shop/coupon-received.png`" class="received-img">
                  </div>
                  <div v-else-if="detail.displayType==='3'" class="received">
                    <span class="receive-disabled" @click.stop="appointmentTip">预约</span>
                  </div>
                  <div v-else-if="detail.displayType==='4'" class="received">
                    <span class="receive-disabled">已设提醒</span>
                  </div>
                  <div v-else-if="detail.displayType==='5'" class="received">
                    <span class="receive-disabled">已抢光</span>
                  </div>
                  <div v-else class="sold-out">
                    <img :src="`${$cdnImageUrl}/shop/coupon-sold-out.png`" class="sold-out-img">
                  </div>
                  <!-- <span class="received-num">已领{{detail.percent}}%</span> -->
                </div>
              </div>
          </div>
          <img class="type-icon" :src="iconSrc" alt="">
          <img class="new-coupon-bg" :src="`${$cdnImageUrl}/shop/yhq-new-coupon-bg.png`" />
        </div>
        <div class="tip-container" v-if="detail && (detail.displayType === '3' || detail.displayType === '4')">
          {{ activityStartDate }}  开始准时开抢！
        </div>
        <!--优惠券（集团卡券）的使用规则是后端返回的-->
        <div class="rulesBg" >
          <div class="titleText">使用规则</div>
          <div class="contentTextElse">
            <p v-for="(item, index) in detail.useRule" :key="index">
              <!-- TODO: fixed here -->
              {{detail.couponFrom === '2' ? `${index + 1}、` : ''}}{{item}}{{detail.couponFrom === '2' ?  '。' : ''}}
            </p>
          </div>
        </div>
        <common-alert
          v-if="showAlert"
          title="请输入预约手机号"
          confirmText="提交"
          cancelText="放弃"
          :showOnConfirm="true"
          @onCancel="hideAlert"
          @onConfirm="appointmentMobile">
          <input type="tel" v-model="phone" maxlength="11" placeholder="请输入数字">
        </common-alert>
      </div>
    </div>
  </div>
</template>
<script>
// import { Progress } from 'vant';
import bannerService from '@/http/service/banner';
import { myManageinfo } from '@/http/service/mine'
import TimeUtils from '../../utils/TimeUtil';
import config from '@/config'
import Skeleton from '@/components/skeleton/index.vue';
import couponValueLabel from '@/components/couponValueLabel'
const commonAlert = () => import('@/components/alert/common-alert.vue');
const localData = {
  hasReceivePersonNum: 0,
  identifyFlag: -1, // 用户身份标识，默认-1 0-普通用户  其他 代表有店的客户
};
/**
 * 优惠券详情页面
 */
export default {
  name: 'couponInfo',
  components: {
    // 'van-progress': Progress,
    commonAlert,
    Skeleton,
    couponValueLabel
  },
  data() {
    return {
      isSkeletonLoading: true,
      detail: {},
      redBg: `url(${config.HOST_PIC}/banner/coupon.png)`,
      blueBg: `url(${config.HOST_PIC}/banner/couponblue.png)`,
      showAlert: false,
      activityStartDate: '',
      phone: '',
    };
  },
  filters: {
    // 将2019-02-03 11:22:00格式的时间戳去掉时分秒，只保留年月日2019-02-03
    removeHHMMSS(val) {
      return val.toString().split(' ')[0];
    }
  },
  computed: {
    isBigVal () {
      return this.detail.couponValue > 1000
    },
    couponIcon () {
      const _couponIcon = {
        ORDER: `${this.$cdnImageUrl}/shop/icon-coupon-card.png`,
        SCENE: `${this.$cdnImageUrl}/shop/icon-coupon-scene.png`,
        // ORDER: `${this.$cdnImageUrl}/shop/icon-coupon-plat.png`,
        SHOP: `${this.$cdnImageUrl}/shop/icon-coupon-shop.png`
      }
      return _couponIcon
    },
    iconSrc () {
      let type = this.detail.couponUseType

      if (this.detail.applyType) type = 'SCENE'

      return this.couponIcon[type] || type
    }

  },
  mounted() {
    document.title = '优惠券详情';
    this.getManageInfo();
    this.getCouponDetail();
  },
  methods: {
    getManageInfo() {
      myManageinfo({}, true).then(({ data }) => {
        console.log('个人信息:', data);
        if (data.success && data.data) {
          localData.identifyFlag = data.data.identifyFlag;
        }
      });
    },
    getCouponDetail() {
      const activityCode = this.$route.query.activityCode;
      const couponTemplateCode = this.$route.query.couponTemplateCode;
      const couponCode = this.$route.query.couponCode;
      const sourceChannel = this.$route.query.sourceChannel;
      const platformChannel = this.$route.query.platformChannel;
      const query = couponCode ? { couponCode, activityCode, couponTemplateCode, sourceChannel, platformChannel } : { activityCode, couponTemplateCode, sourceChannel, platformChannel }

      bannerService.getCouponDetail(query).then(res => {
        if (res.data.success) {
          this.isSkeletonLoading = false;
          // 使用规则内容格式重排
          let _resData = res.data.data;
          if (_resData.couponFrom === '1') {
            let useRule = _resData.useRule.replace(/\[|]/g, '').replace(/"/g, '');
            _resData.useRule = useRule.split(',');
          } else {
            let newUseRule = [];
            const useRule = JSON.stringify(_resData.useRule).split('。');
            useRule.map(item => {
              if (item) {
                newUseRule.push(item);
              }
            })
            _resData.useRule = newUseRule;
          }

          this.detail = _resData;
          localData.hasReceivePersonNum = _resData.reQuliaty && _resData.reQuliaty;
          this.activityStartDate = this.detail && _resData.activityStartTime && TimeUtils.formatFullDate(new Date(res.data.data.activityStartTime * 1000));
        } else {
          this.detail = null;
        }
      }).catch(() => {
        this.detail = null;
      });
    },
    getCoupon() {
      // 登陆判断
      if (!this.$store.getters.getLoginStatus) {
        this.$store.dispatch('feedback', this.$route.path);
        this.$store.dispatch('doLogin');
        return
      }
      const streetId = this.$store.getters['getlocationInfo'].townshipId;
      const activityCode = this.$route.query.activityCode;
      const couponTemplateCode = this.$route.query.couponTemplateCode;
      const couponCode = this.$route.query.couponCode;
      const sourceChannel = this.$route.query.sourceChannel;
      const platformChannel = this.$route.query.platformChannel;
      const query = couponCode ? { couponCode, activityCode, couponTemplateCode, sourceChannel, platformChannel } : { activityCode, couponTemplateCode, sourceChannel, platformChannel }

      bannerService.getCoupon({
        ...query,
        streetId,
      }).then(res => {
        // this.$toast(res.data.message);
        if (res.data.success) {
          this.$toast('领取成功');
          this.detail.displayType = '2';
          localData.hasReceivePersonNum = res.data.data.hasReceivePersonNum;
          this.showRNTip();
          // this.getCouponDetail();
        } else {
          this.$toast(res.data.message);
        }
      });
    },
    useCoupon(data) {
      if (!this.showRNTip()) {
        this.$router.push(`/usablecouponlist?activityCode=${this.detail.activityCode}&couponTemplateCode=${this.$route.query.couponTemplateCode}&couponType=${data.couponType}&sourceChannel=${this.$route.query.sourceChannel}&platformChannel=${this.$route.query.platformChannel || ''}`);
      }
    },
    showRNTip() {
      if (this.$util.isSgRn() && localData.identifyFlag === 0) {
        const activityCode = this.$route.query.activityCode;
        const couponTemplateCode = this.$route.query.couponTemplateCode;
        const couponCode = this.$route.query.couponCode;
        const sourceChannel = this.$route.query.sourceChannel;
        const platformChannel = this.$route.query.platformChannel;
        const query = couponCode ? { couponCode, activityCode, couponTemplateCode, sourceChannel, platformChannel } : { activityCode, couponTemplateCode, sourceChannel, platformChannel }

        window.postMessage(JSON.stringify({
          type: 'showCouponTip',
          data: {
            ...query
          }
        }));
        return true;
      }
      return false;
    },
    appointmentTip() {
      this.showAlert = true;
    },
    hideAlert() {
      this.showAlert = false;
    },
    appointmentMobile() {
      if (this.phone.length < 11) {
        this.$toast('请输入正确的手机号');
        return;
      }
      this.showAlert = false;
      bannerService.setCouponRemind({
        phone: this.phone,
        couponId: this.detail.id
      }).then(res => {
        if (res.data.success) {
          this.$toast('预约成功！');
          this.getCouponDetail();
        }
      });
    }
  },
}
</script>
